import { useState, useEffect, useRef } from 'react';
import Loader from './Loader';
import Chart from './Chart';

const TodaysGraph = (props) => {
	const [graphHeight, setGraphHeight] = useState(270)
	const graphRef = useRef()

	useEffect(()=>
		setGraphHeight(graphRef.current.offsetHeight)
	)
	return (
		<div className='todaysGraph' ref={graphRef}>
			<div className='graphsTitle'>Прогноз на два дня</div>
			<div className='chartGraph' >
			{props.isPostsLoading?<Loader style={{width:'100%'}}/>:<Chart height={graphHeight} hours={props.hours} hourly={props.hourly}></Chart>}
			</div>
			
		</div>
	);
};

export default TodaysGraph;