import React from 'react';
import cl from '../styles/ModalToday.module.css'
import { useState, useEffect } from 'react';
const ModalToday = (props) => {
	const [img, setImg] = useState('');
	
	
	const fetchImage = async (cod) => {
		const imageUrl = "https://openweathermap.org/img/wn/"+cod+"@2x.png"
		const res = await fetch(imageUrl);
		const imageBlob = await res.blob();
		const imageObjectURL = URL.createObjectURL(imageBlob);
		setImg(imageObjectURL);
	}
	const place=props.place;
	const lat = props.lat
	const lon = props.lon
	let strPlace = ''
	let icon = ''
	let date = ''
	let deg = ''
	let humidity = ''
	let dewPoint = ''
	let pressure = ''
	let uvi =''
	let visibility =''
	let speed = ''
	let direction=''
	let temperature = ''
	let feelsLike = ''
	let weatherDescription=''
	const directions = ['↑ N', '↗ NE', '→ E', '↘ SE', '↓ S', '↙ SW', '← W', '↖ NW'];
	const current = props.data===''?'':JSON.parse(props.data).daily[props.dayClicked]
	let daily =''
	let maxTemp ='' 
	let minTemp =''
	let sr =''
	let sunrise ='' 
	let ss =''
	let sunset =''
	let mr  =''
	let moonrise =''
	let ms  =''
	let moonset =''
	const dt = typeof current==='object'? current.dt:''
	if (dt!==''){
		const d = new Date(dt * 1000);
		date = d.toDateString();

		temperature = (current.temp.day - 273.15).toFixed(0) + '°C';
		icon = current.weather[0].icon;
		feelsLike ='Feels like: '+(current.feels_like.day - 273.15).toFixed(0) + '°C'
		weatherDescription = current.weather[0].description
		weatherDescription = weatherDescription[0].toUpperCase() + weatherDescription.slice(1)
		deg = current.wind_deg;
		direction = directions[Math.round(deg / 45) % 8];
		speed = '  ' + current.wind_speed  +'m/s'
		humidity = current.humidity
		dewPoint = (current.dew_point - 273.15).toFixed(0)
		pressure = current.pressure
		uvi = current.uvi
		daily = JSON.parse(props.data).daily
		maxTemp = parseInt(daily[0].temp.max-273.15);
		minTemp = parseInt(daily[0].temp.min-273.15);
		sr = new Date((daily[0].sunrise-(10800 - props.timezone)) *1000)
		sunrise = sr.getHours()+':'+sr.getMinutes()
		ss = new Date((daily[0].sunset-(10800 -props.timezone)) *1000)
		sunset = ss.getHours()+':'+ss.getMinutes()
		mr = new Date((daily[0].moonrise-(10800 - props.timezone)) *1000)
		moonrise =  mr.getHours()+':'+mr.getMinutes()
		ms = new Date((daily[0].moonset-(10800-props.timezone)) *1000)
		moonset =  ms.getHours()+':'+ms.getMinutes()}
	if (typeof place==='object'){
		strPlace = place[0].name + ', ' + place[0].country;
	}
	useEffect(() => {
		fetchImage(current.weather[0].icon);
	  });
	return (
		<button onClick={()=>{props.setDayClicked(0)}} className={cl.today}>
		<div className={cl.mainInfo}>

			<div className='dateplace'>
				<div style={{fontSize:'20px'}} className={cl.datetime} >
					{date}
				</div>
				<div className={cl.place}>
					{strPlace}
				</div>
			</div>
			<div className={cl.temperature}>
				<img src = {img}></img>
				<div>{temperature}</div>
			</div>
		</div>

		<div style={{fontSize:'18px'}} className={cl.feels}>
			<span>{feelsLike}. </span>
			<span>{weatherDescription}</span>
		</div>
		<div className={cl.conditions}>
			<div className='wind'>
				{direction} {speed}
			</div>
			<div className='humidity'>
			 Humidity:{humidity} %
			</div>
			<div className='dewPoint'>
			Dew point:{dewPoint} °C
			</div>
			<div className='pressure'>
			<svg data-v-7bdd0738="" data-v-3208ab85="" width="12px" height="12px" viewBox="0 0 96 96" className="icon-pressure"><g data-v-7bdd0738="" transform="translate(0,96) scale(0.100000,-0.100000)" fill="#48484a" stroke="none"><path data-v-7bdd0738="" d="M351 854 c-98 -35 -179 -108 -227 -202 -27 -53 -29 -65 -29 -172 0
-107 2 -119 29 -172 38 -75 104 -141 180 -181 58 -31 66 -32 176 -32 110 0
118 1 175 32 77 40 138 101 178 178 31 57 32 65 32 175 0 110 -1 118 -32 176
-40 76 -106 142 -181 179 -49 25 -71 29 -157 32 -73 2 -112 -1 -144 -13z m259
-80 c73 -34 126 -86 161 -159 24 -50 29 -73 29 -135 0 -62 -5 -85 -29 -135
-57 -119 -161 -185 -291 -185 -130 0 -234 66 -291 185 -24 50 -29 73 -29 135
0 130 66 234 185 291 82 40 184 41 265 3z"></path><path data-v-7bdd0738="" d="M545 600 c-35 -35 -68 -60 -80 -60 -27 0 -45 -18 -45 -45 0 -33 -50
-75 -89 -75 -18 0 -41 -5 -53 -11 -20 -11 -20 -11 3 -35 12 -13 33 -24 46 -24
17 0 23 -6 23 -23 0 -13 10 -33 23 -45 30 -28 47 -13 47 43 0 32 6 47 28 68
15 15 37 27 48 27 26 0 44 18 44 44 0 12 26 47 60 81 l60 61 -28 27 -28 27
-59 -60z"></path></g></svg>{pressure} hPa
				
			</div>
			<div className='uvi'>
			UV: {uvi}
			</div>
			<div className='visibility'>
			</div>
			<div className='maxTemp'>
			max temp: {maxTemp} °C
			</div>
			<div className='minTemp'>
			min temp: {minTemp} °C
			</div>
			<div className='sunrise'>
			Восход солнца: {sunrise}
			</div>
			<div className='sunset'>
			Заход солнца: {sunset}
			</div>
			<div className='moonrise'>
			Появление луны: {moonrise}
			</div>
			<div className='moonset'>
			Заход луны: {moonset}
			</div>

		</div>
	</button>
	);
};

export default ModalToday;