import React from 'react';
import {useState, useEffect} from 'react'
import Chart from './Chart.jsx';
import Highchart from './Highchart.jsx';
import {timezone,timedelta} from 'datetime'
import Today from './Today.jsx';
import ModalToday from './ModalToday.jsx';
import Tape from './Tape.jsx'
import Loader from './Loader.jsx';
import TodaysGraph from './TodaysGraph.jsx';


const Cards = (props) => {
	const isPostsLoading = props.isPostsLoading;
	const place=props.place;
	// const lat = props.lat
	// const lon = props.lon
	let strPlace = ''
	const daily = props.daily
	let dayData = '12345678'
	let tapes = '12'
	const [dayClicked, setDayClicked]=useState(Boolean(0))
	if (typeof daily === 'object'){
		dayData = daily
		tapes =  daily.map((item, move)=>{
		return isPostsLoading
		?<Loader style={{width:'100%', height:'100%', marginBottom:'3px', paddingTop:'0px'}}/>
		:<Tape setDayClicked={setDayClicked} key={move} number={move} timezone={props.timezone} data={dayData[move]}/>
		})
	} else{
		<Loader style={{width:'100%', height:'100%', marginBottom:'3px', paddingTop:'0px'}}/>
	}
	
	// const tapes = <Today timezone={props.timezone} hours={props.hours} hourly={props.hourly} city={props.city} img = {props.img} data={props.data} place = {props.place} lat={props.lat} lon={props.lon}></Today>
	return (
		<div className = 'Cards'>
			{isPostsLoading?<Loader id={'loader1'}/>:<Today timezone={props.timezone} city={props.city} img = {props.img} data={props.data} place = {props.place} ></Today>}
			<TodaysGraph isPostsLoading={isPostsLoading} hours={props.hours} hourly={props.hourly}/>
			<div className='weekCard'>
				<div className='forecastTitle'>Прогноз на неделю</div>
				<div className='forecastOtherDays'>
					{isPostsLoading?<Loader/>:dayClicked?<ModalToday dayClicked={dayClicked} setDayClicked={setDayClicked}  timezone={props.timezone} city={props.city} img = {props.img} data={props.data} place = {props.place} lat={props.lat} lon={props.lon}></ModalToday>
					:tapes}
						{/* {JSON.stringify(JSON.parse(props.data).daily)} */}
				</div>
			</div>
	</div>
	);
};



export default Cards;