import React from 'react';
import {useState, useEffect} from 'react';
import './styles/App.css';
import load from './load';
import Queries from './components/Queries';
import Cards from './components/Cards';
import Loader from './components/Loader';

// import load from './load';

function App() {
	const [isPostsLoading, setIsPostsLoading] = useState(false)
	const [img, setImg] = useState('');
	const [data, setData] = useState('')
	const [city, setCity] = useState('London')
	const [hourly, setHourly] = useState('')
	const [hours, setHours] = useState('')
	const [timezone, setTimezone] = useState('')
	const [daily, setDaily] = useState('')
	// const [citiesArray, setCitiesArray] = useState([''])
	const [placeData, setPlaceData] = useState('')
	
	async function fetchImage(cod) {
		const imageUrl = "https://openweathermap.org/img/wn/" + cod + "@2x.png";
		const res = await fetch(imageUrl);
		const imageBlob = await res.blob();
		const imageObjectURL = URL.createObjectURL(imageBlob);
		setImg(imageObjectURL);
	}
	

	async function weatherDataSetting(city) {
		setIsPostsLoading(true)
		setTimeout(async ()=>{		
			const api = '542f49cf0cccb27eb8ccbf385fded39f'
			let cod = ''
			const URL = "https://api.openweathermap.org/geo/1.0/direct?q=" +
			city +
			"&limit=1&appid="+api;
	  
			let response = await fetch(URL)
			let placeObj = await response.json()
			setPlaceData(placeObj)
			const ABC = "https://api.openweathermap.org/data/2.5/onecall?lat=" +
			placeObj[0].lat + "&lon=" + placeObj[0].lon +
			"&exclude=minutely,alerts&appid="+api;
			let res = await fetch(ABC)
			let json = await res.json()
			setData(json)
			cod = json.current.weather[0].icon;
			const hourlyTempArray = []
			const hoursArray = []
			setTimezone(json.timezone_offset)
			json.hourly.forEach(function(obj)
				{hourlyTempArray.push(obj["temp"]); 
				hoursArray.push(obj["dt"])})
				const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
										"Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
			  setDaily(json.daily)
			const newHours = hoursArray.map(x=>{
			  let date = new Date((x-(10800-json.timezone_offset)) * 1000)
			  date = date.getDate().toString() +' '+ monthNames[date.getMonth()] +' '+ date.getHours() + ':00'
			  return date
		  })
			setHours(newHours)
			setHourly(hourlyTempArray)
			
			fetchImage(cod)
			setIsPostsLoading(false)
		}, 2000)

	} 

	useEffect(() =>{
		weatherDataSetting(city)
	}, [])

  return (
    <div className="App">

		<div className='title'>Напиши город - узнай погоду</div>
		<Queries cities={['', 'London', 'New York', 'Cheboksary', 'Moscow']} weatherDataSetting={weatherDataSetting}/>
		<Cards isPostsLoading={isPostsLoading} daily={daily} timezone={timezone} hours={hours} hourly={hourly} city={city} img = {img} data={JSON.stringify(data)} place = {placeData}/>
	</div>
  );
}

export default App;
