import React from 'react';
import { useState, useEffect } from 'react';
import cl from '../styles/Tape.module.css'

const Tape = (props) => {
	const [img, setImg] = useState('');
	const fetchImage = async (cod) => {
		const imageUrl = "https://openweathermap.org/img/wn/"+cod+"@2x.png"
		const res = await fetch(imageUrl);
		const imageBlob = await res.blob();
		const imageObjectURL = URL.createObjectURL(imageBlob);
		setImg(imageObjectURL);
	}

	const data = props.data
	let dt = ''
	let dtSmall=''
	let feelsLike=''
	let ws = ''
	const directions = ['N', 'NE', 'E', 'SE', 'S', 'SW', 'W', 'NW'];
	let direction=''
	let description = ''
	let deg = ''
	let temp=''
	let morn = ''
	let day = ''
	let eve = ''
	if (typeof data === 'object'){

		dt = new Date((data.dt-(10800-props.timezone))*1000)
		dt = dt.toDateString()
		dtSmall = dt.slice(0,dt.length-4)
		// feelsLike= ' Утром: ' +(data.feels_like.morn - 273.15).toFixed(1)+
		// '°C  Днём ' + (data.feels_like.day - 273.15).toFixed(1)+
		// '°C  Вечером ' + (data.feels_like.eve - 273.15).toFixed(1) +'°C'
		morn = ' Утром: ' +(data.feels_like.morn - 273.15).toFixed(1) + '°C  '
		day = 'Днём ' + (data.feels_like.day - 273.15).toFixed(1)+'°C  '
		eve = '  Вечером ' + (data.feels_like.eve - 273.15).toFixed(1) +'°C '
		direction = directions[Math.round(data.wind_deg / 45) % 8];
		deg = data.wind_deg - 45
		ws = '  '+ data.wind_speed + ' m/s';
		temp = (data.temp.day - 273.15). toFixed(0) + '°C'
		description = data.weather[0].description;
		description = description[0].toUpperCase() + description.slice(1) + '  '
	}
	useEffect(() => {
		fetchImage(data.weather[0].icon);
	  });
	return (
		<button  onClick={()=>{props.setDayClicked(props.number)}} className={cl.tape}>
			<div className={cl.datetemp}>
				<div className={cl.datetime}> {dtSmall} </div>
				<div className={cl.temp}>
					<img src = {img} style={{width:'40px', height:'40px'}}></img>
					<div>{temp}</div>
				</div>
			</div>
			<div className={cl.description}>
				{description}
				<img style={{transform:`rotate(${deg}deg)`, width:'15px'}} src="data:image/png;base64,"/>
				{ws}
			</div>

			<div className={cl.body}>
				<span style={{textAlign:'left', fontSize:'1.1vw'}}>Ощущается:</span>
				<span >{morn}</span>
				<br></br>
				<span>{day}</span>
				<span>{eve}</span>

			</div>
			<div></div>
		</button>
	);
};

export default Tape;