import React from 'react';
import {useState} from 'react'

const Queries = (props) => {
	const cities = props.cities
	const [inputValue, setInputValue] = useState('London')
	// const getCityName=(value)=>{
	// 	props.weatherDataSetting(value)
	// 	setCityState(value)
	// 	console.log(value)
	// }
	return (
		<div className="queries">
			<input value ={inputValue} onChange={e=>setInputValue(e.target.value)}></input>
			{/* <button className='btn'>Добавить в частые</button> */}
				<select onChange={e=>setInputValue(e.target.value)}>
					{
						cities.map((step, city) =>{
							return(
								<option key={city}>{cities[city]}</option>
							)
						})
					}
				</select>
				<button className='btn' onClick={()=>{props.weatherDataSetting(inputValue);
				 setInputValue('');}}>Нажми, узнаешь</button>
		</div>
	);
};

export default Queries;