import React from 'react';
import cl from '../styles/Loader.module.css'
const Loader = (props) => {

	return (
		<div className='loader' id = {props.id} style={props.style}>
			<div className={cl.bubblingG}>
				<span id={cl.bubblingG_1}>
				</span>
				<span id={cl.bubblingG_2}>
				</span>
				<span id={cl.bubblingG_3}>
				</span>
			</div>
		</div>
	);
};

export default Loader;