import React, { Component } from 'react'
import * as d3 from "d3"
// import {scaleLinear} from "d3-scale";
// import classNames from 'classnames'
import cl from '../styles/Chart.module.css'

export default class Chart extends Component {
	constructor(props) {
		super(props);
		this.state = {height: props.height};
	  }
	  
  render() {
	function getMaxOfArray(numArray) {
		return Math.max.apply(null, numArray);
	  }
	function getMinOfArray(numArray) {	
		return Math.min.apply(100, numArray);
	  }
	const hours = Array.isArray(this.props.hours)?this.props.hours:[];
	const hourly = Array.isArray(this.props.hourly)?this.props.hourly.map(x=>parseInt(x-273.15)):[];
	const getY = d3.scaleLinear()
		.domain([Math.min(getMinOfArray(hourly)-2, 0), getMaxOfArray(hourly)+2])
		.range([this.state.height*0.6, 20]);
	const getX = d3.scaleBand()
		.domain(hours)
		.range([10, 4000]);
	const data = []

	for (let i=0; i<hours.length; i++){
		let elem = {}
		elem.name = hours[i]
		elem.value = hourly[i]
		
		data.push(elem)
	}

	const getYAxis = ref => {
		const yAxis = d3.axisLeft(getY);
		d3.select(ref).call(yAxis);
	  };
	  
	  const getXAxis = ref => {
		const xAxis = d3.axisBottom(getX);
		d3.select(ref).call(xAxis);
	  };
	  const linePath = d3
		.line()
		.x(d => getX(d.name) + getX.bandwidth() / 2)
		.y(d => getY(d.value))
		.curve(d3.curveMonotoneX)(data);
	
	const areaPath = d3.area()
		.x(d => getX(d.name) + getX.bandwidth() / 2)
		.y0(d => getY(d.value))
		.y1(() => getY(0))
		.curve(d3.curveMonotoneX)(data);
	return (
		<svg style={{marginTop:'30px', padding:'0px'}} width={3985} height={this.state.height*0.68}>
		    <path
				strokeWidth={3}
				fill="none"
				stroke="#7cb5ec"
				d={linePath}
			/>
			<path
				fill="#7cb5ec"
				d={areaPath}
				opacity={0.2}
			/>
			<g className = {cl.axis} ref={getYAxis} transform={`translate(20,0)`} />
			<g
			className={ (cl.axis, cl.xAxis)}
			ref={getXAxis}
			transform={`translate(0,${getY(0.01)})`} // нужно сдвинуть ось в самый низ svg
			/>
			{data.map((item, index) => {
			return (
				<g key={index}>
					<circle
						key={index}
						cx={getX(item.name) + getX.bandwidth() / 2}
						cy={getY(item.value)}
						r={5}
						fill="red"
						/>
					<text
						fill="#666"
						x={getX(item.name) + getX.bandwidth() / 2}
						y={getY(item.value) - 10}
						textAnchor="middle"
					> {item.value}
					</text>
				</g>
			);
			})}
		</svg>
	);
  }
}
